
/* === Hero Section === */
.hero {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 80vh;
    padding: 0 2rem;
    color: var(--white-color);
    background: linear-gradient(rgba(0, 45, 98, 0.7), rgba(0, 45, 98, 0.7)), 
                url('https://images.unsplash.com/photo-1554968363-a2b9d3d3a067?q=80&w=2070') no-repeat center center/cover;
}

.hero-title {
    font-size: 3.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
    text-shadow: 2px 2px 8px rgba(0,0,0,0.5);
}

.hero-subtitle {
    font-size: 1.25rem;
    font-weight: 300;
    margin-bottom: 2rem;
    max-width: 600px;
}

.hero-cta-buttons {
    display: flex;
    gap: 1rem;
}

/* === Orange Button (Updated) === */
.cta-button {
    padding: 12px 30px;
    border-radius: 8px;
    text-decoration: none;
    font-weight: 600;
    transition: all 0.3s ease;
}

.cta-primary {
    background-color: #f47c3c; /* Orange */
    color: white;
    border: none;
}

.cta-primary:hover {
    background-color: #e06e32;
    transform: translateY(-3px);
}

/* === Features Section === */
.features-section {
    padding: 5rem 2rem;
    text-align: center;
    background-color: var(--white-color);
}

.section-title {
    font-size: 2.5rem;
    font-weight: 600;
    color: var(--primary-color);
    margin-bottom: 1rem;
}

.section-subtitle {
    font-size: 1.1rem;
    color: #666;
    margin-bottom: 3rem;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
}

.features-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
    max-width: 1200px;
    margin: 0 auto;
}

.feature-card {
    background-color: white;
    padding: 2rem;
    border-radius: 12px;
    border: 1px solid #eee;
    box-shadow: 0 4px 8px rgba(0,0,0,0.05);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.feature-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 16px rgba(0,0,0,0.1);
}

.feature-icon {
    font-size: 3rem;
    color: var(--primary-color);
    margin-bottom: 1.5rem;
}

.feature-card h3 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
}

.feature-card p {
    font-size: 1rem;
    color: #555;
}

/* === Footer === */
.footer {
    padding: 2rem;
    text-align: center;
    background-color: #222;
    color: #aaa;
}
