.dashboard-container {
    display: flex;
    min-height: 100vh;
}

/* === Sidebar === */
.sidebar {
    width: 260px;
    background-color: #2d3436; /* Dark background from our theme */
    color: var(--white-color);
    display: flex;
    flex-direction: column;
    padding: 1.5rem 0;
    transition: width 0.3s ease;
}

.sidebar-header {
    padding: 0 1.5rem 1.5rem 1.5rem;
    font-size: 1.5rem;
    font-weight: 600;
    color: var(--primary-color);
    border-bottom: 1px solid #444;
}

.sidebar-nav {
    list-style: none;
    margin-top: 1.5rem;
    flex-grow: 1;
}

.nav-item a {
    display: flex;
    align-items: center;
    padding: 1rem 1.5rem;
    color: #bdc3c7;
    text-decoration: none;
    transition: background-color 0.2s, color 0.2s;
}

.nav-item a:hover {
    background-color: #3a4245;
    color: var(--white-color);
}

.nav-item a.active {
    background-color: var(--primary-color);
    color: var(--white-color);
    font-weight: 500;
}

.nav-icon {
    margin-right: 1rem;
    width: 20px; /* Aligns icons neatly */
}

/* === Main Content === */
.main-content-wrapper {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.dashboard-header {
    background: var(--white-color);
    padding: 1rem 2rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    box-shadow: 0 2px 5px rgba(0,0,0,0.05);
    height: 80px;
}

.logout-button {
    background-color: var(--primary-color);
    color: var(--white-color);
    border: none;
    padding: 10px 20px;
    border-radius: 6px;
    font-family: 'Poppins', sans-serif;
    font-size: 1rem;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.3s;
}

.logout-button:hover {
    background-color: var(--primary-hover);
}

.dashboard-content {
    flex-grow: 1;
    padding: 2rem;
    background-color: var(--secondary-color);
}