.profile-container {
    max-width: 800px;
}

.profile-card {
    background: var(--white-color);
    padding: 2rem;
    border-radius: 12px;
    box-shadow: var(--shadow-light);
}

.profile-card h2 {
    font-size: 1.8rem;
    color: var(--primary-color);
    margin-bottom: 1.5rem;
    border-bottom: 1px solid var(--border-color);
    padding-bottom: 1rem;
}

.profile-info p {
    font-size: 1.1rem;
    color: #555;
    margin-bottom: 1rem;
}

.profile-info strong {
    color: var(--text-color);
    margin-right: 10px;
    font-weight: 500;
}