.admin-dashboard-container {
    display: flex;
    min-height: 100vh;
    background-color: #f4f7fa;
}

.admin-sidebar {
    width: 260px;
    background-color: #34495e;
    color: #ecf0f1;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.admin-sidebar-header {
    padding: 1.5rem;
    font-size: 1.6rem;
    font-weight: 600;
    text-align: center;
    color: #fff;
    background-color: #2c3e50;
}

.admin-sidebar-nav {
    list-style: none;
    margin-top: 1rem;
    flex-grow: 1;
}

.admin-nav-item a {
    display: flex;
    align-items: center;
    padding: 1rem 1.5rem;
    color: #bdc3c7;
    text-decoration: none;
    transition: background-color 0.2s, color 0.2s;
    border-left: 4px solid transparent;
}

.admin-nav-item a:hover {
    background-color: #2c3e50;
    color: #fff;
}

.admin-nav-item a.active {
    background-color: #2c3e50;
    color: #fff;
    font-weight: 500;
    border-left: 4px solid #9b59b6;
}

.admin-nav-icon {
    margin-right: 1rem;
    width: 20px;
    text-align: center;
}

.admin-main-wrapper {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.admin-header {
    background: #9b59b6;
    color: #fff;
    padding: 1rem 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    height: 80px;
}

.admin-header h1 {
    font-size: 1.5rem;
    font-weight: 500;
}

.admin-user-info {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.admin-user-info .logout-button {
    background: #8e44ad;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 6px;
    font-family: 'Poppins', sans-serif;
    font-size: 1rem;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.3s;
}

.admin-user-info .logout-button:hover {
    background: #732d91;
}

.admin-page-content {
    flex-grow: 1;
    padding: 2rem;
    overflow-y: auto;
} 
/* AdminDashboardLayout.css */

/* Default sidebar link */
.admin-sidebar-nav .admin-nav-item a {
    display: flex;
    align-items: center;
    padding: 10px 15px;
    color: #fff;
    text-decoration: none;
    transition: background 0.3s;
}

/* Hover effect */
.admin-sidebar-nav .admin-nav-item a:hover {
    background-color: #1f2937; /* Slightly darker */
    border-radius: 5px;
}

/* Active link */
.admin-sidebar-nav .admin-nav-item a.active {
    background-color: #2563eb; /* Blue for active */
    border-radius: 5px;
    font-weight: bold;
}

/* Icon spacing */
.admin-nav-icon {
    margin-right: 10px;
}
